@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html, #root, .app{
    height: 100%;
}
html, body {width:100%; overflow-x:hidden}
body{
    height: 100%;
    background: #2B2440;
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    font-weight: 400;
    color: #F5F5F5;
    line-height: 1.2;
}
a, button{
    cursor: pointer;
    text-decoration: none;
}
h1{
    position: relative;
    z-index: 3;
}
button{
    position: relative;
    z-index: 2;
    background: none;
    outline: none;
    border: none;
    font-size: 1.125em;
    color: rgba(245, 245, 245, 0.50);
}
button.default-btn{
    &::before{
        transition: all .3s;
        cursor: default;
        content: '';
        position: absolute;
        width: 2px;
        height: 123px;
        transform: rotate(90deg);
        flex-shrink: 0;
        left: 50%;
        bottom: -4.52em;
        background: linear-gradient(180deg, rgba(255, 88, 95, 0.00) 0%, #FA5897 51.57%, rgba(255, 88, 95, 0.00) 100%);
        opacity: 0;
    }
}
button.active{
    color: #F5F5F5;
    transition: all .3s;
    &::before{
        opacity: 1;
    }
}
input{
    outline: none;
    color: #F5F5F5;
    font-weight: 600;
    background: #2B2440;
    border: 2px solid #FA5897;
}
.input{
    padding: 1em;
    border-radius: 12px;
    display: flex;
    align-items: center;
    height: 3.25em;
    width: 20em;
    border: 2px solid #FA5897;
    transition: all .25s;
    &:hover, &:focus{
        transition: all .25s;
        border: 2px solid #FF585F;
        box-shadow: 0 0 5pt 1pt #FF585F !important;
    }
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    display: none;
    -webkit-appearance: none;
    margin: 0;
}
.custom-select{
    height: 43px;
    width: 15em;
    position: relative;
    z-index: 3;
    & .react-select__control{
        height: 100%;
        border: 2px solid #FA5897;
        background: #2B2440;
        border-radius: 12px;
        box-shadow: none;
        transition: all .25s;
        &:hover{
            transition: all .25s;
            border: 2px solid #FF585F;
            box-shadow: 0 0 5pt 1pt #FF585F !important;
            .css-1xc3v61-indicatorContainer{
                color: #FF585F;
            }
        }
        .css-1xc3v61-indicatorContainer{
            color: #FFFFFF;
        }
        .css-15lsz6c-indicatorContainer{
            color: #FF585F;
        }
    }
    & .react-select__input-container, .react-select__single-value{
        color: #F5F5F5;
    }
    & .react-select__option{
        border-bottom: 2px solid #FA5897;
        background: #2B2440;
        cursor: pointer;
        &:last-child{
            border-bottom: none;
        }
        &:active{
            background: none;
        }
    }
    & .react-select__menu{
        border: 2px solid #FA5897;
        padding: 0 5px;
        border-radius: 7px;
        background: #2B2440;
    }
}
.swap-btn{
    transition: box-shadow .25s;
    &:hover{
        box-shadow: 0 0 5pt 1pt #FF585F;
        transition: box-shadow .25s;
    }
}
.custom-select{
    position: relative;
}
.container{
    max-width: 1250px;
    margin: 0 auto;
    height: 100%;
    width: 100%;
}
h1{
    font-size: 1.375em;
    font-weight: 700;
    text-align: center;
}
.converter-wrapper{
    position: relative;
    width: 1024px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: stretch;
    height: 100%;
    z-index: 2;
    &::before, &::after{
        content: '';
        position: absolute;
    }
    &::before{
        width: 9em;
        height: 9em;
        right: 1em;
        top: 11em;
        z-index: 3;
        background: url("./../public/bg1.png") no-repeat;
        background-size: contain;
    }
    &::after{
        z-index: 1;
        width: 13.75em;
        height: 13.75em;
        left: -10em;
        top: 38%;
        background: url("./../public/bg2.png") no-repeat;
        background-size: contain;
    }
}
.converter{
    position: relative;
    z-index: 2;
    margin-top: 1.75em;
    padding: 2em 2.8em;
    width: 100%;
    border-radius: 12px;
    background: linear-gradient(180deg, #271D35 0%, rgba(39, 29, 53, 0.20) 100%);
    backdrop-filter: blur(5px);
}
.btns{
    position: relative;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    column-gap: 3.37em;
    width: 64.84%;
    &::before{
        content: '';
        position: absolute;
        width: 2px;
        height: 664px;
        transform: rotate(90deg);
        flex-shrink: 0;
        left: 50%;
        top: -18.2em;
        opacity: 0.2;
        background: linear-gradient(180deg, rgba(217, 217, 217, 0.00) 12.5%, #FFF 51.57%, rgba(255, 255, 255, 0.00) 86.98%);
    }
}
.content{
    margin-top: 2.43em;
}
.convert-items{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.convert-item{
    & span{
        font-size: 13px;
        color: #B7B7B7;
    }
    & input, & .custom-select{
        margin-top: .625em;
    }
    & button{
        position: relative;
        top: .84em;
        width: 3.25em;
        height: 3.25em;
        border-radius: 100%;
        background: #2B2440;
        display: flex;
        align-items: center;
        font-size: 1em;
        justify-content: center;
    }
}
.convert-dates{
    margin-top: 1.56em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 4.1em;
}
.convert-btn{
    border-radius: 12px;
    background: linear-gradient(97deg, #FF585F 3.28%, #FA5897 95.35%);
    width: 8.31em;
    height: 3.125em;
    font-size: 1.06em;
    font-weight: 500;
    color: #FFFFFF;
}
.dates-wrapper{
    display: flex;
    align-items: flex-end;
    column-gap: 1.5em;
    .date{
        margin-bottom: 5px;
    }
}
.dates{
    display: flex;
    column-gap: 14px;
    align-items: center;
    .equal{
        width: 30px;
        height: 30px;
        background: #2B2440;
        border-radius: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}
.data-item{
    padding: 12px;
    border-radius: 12px;
    background: #1E192D;
    display: flex;
    column-gap: 16px;
    & p{
        margin-top: 2px;
        font-size: 12px;
        color: rgba(245, 245, 245, 0.50);
        width: 80px;
    }
    & span{
        font-size: 14px;
        font-weight: 500;
    }
    .amount{
        margin-top: 6px;
        font-size: 1.18em;
        font-weight: 700;
        color: #FFF;
    }
}
.date{
    width: 8em;
    font-size: 13px;
    font-weight: 700;
}
.history-items{
    display: flex;
    column-gap: 1em;
    justify-content: space-between;
    .custom-select{
        width: 145px;
    }
}
.history-selects{
    display: flex;
    column-gap: 1.25em;
}
.history-dates{
    display: flex;
    column-gap: 1em;
    & input{
        width: 166px;
    }
}
.error-data{
    position: absolute;
    top: 2em;
    width: 100%;
    left: 0;
    .container{
        max-width: 1024px;
        display: flex;
        flex-direction: column;
        row-gap: 1em;
    }
    & p{
        padding: 1em;
        border: 1px solid #FF585F;
        border-radius: 7px;
        font-weight: 700;
    }
}

@media (max-width: 1200px) {
    .converter-wrapper{
        max-width: 900px;
    }
    .input{
        width: 14em;
    }
    .history-dates input{
        width: 130px;
    }
    .converter-wrapper::before{
        top: 11em;
    }
}
@media (max-width: 964px) {
    body{
        font-size: 15px;
    }
    .converter-wrapper{
        max-width: 700px;
    }
    .input{
        width: 10em;
        height: 2.8em;
    }
    .custom-select{
        width: 12em;
        height: 2.8em;
    }
    .converter-wrapper::before{
        top: 5em;
    }
    .history-dates input{
        width: 100px;
        height: 2.8em;
    }
    .converter{
        padding: 1.4em;
    }
    .convert-btn{
        width: 7.31em;
        height: 2.8em;
    }
    .dates{
        column-gap: 7px;
    }
    .dates-wrapper{
        column-gap: 12px;
    }
    .data-item .amount{
        font-size: 1.1em;
    }
    .date{
        font-size: 12px;
    }
    .btns::before{
        top: -19.9em;
    }
}

@media (max-width: 768px){
    .converter-wrapper{
        max-width: 100%;
        width: 100%;
        &::before{
            z-index: 1;
        }
    }
    .container{
        max-width: 100%;
        padding: 0 15px;
    }
    .input{
        width: 100%;
        height: 36px;
    }
    .custom-select{
        width: 100%;
        height: 36px;
    }
    .history-items{
        flex-wrap: wrap;
        row-gap: 2em;
    }
    .data-item{
        display: block;
        padding: 7px;
        .amount{
            font-size: 1em;
        }
        & span{
            font-size: 12px;
        }
    }
    .convert-item button{
        width: 2.5em;
        height: 2.5em;
        padding: 7px;
        & img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    .converter{
        padding: 15px;
    }
    .convert-items{
        column-gap: 15px;
        .convert-item{
            width: 100%;
            &:nth-child(3){
                width: auto;
            }
        }
    }
    .history-selects, .history-dates{
        width: 100%;
    }
    .history-selects{
        .convert-item{
            width: 100%;
            .custom-select{
                width: 100%;
            }
        }
    }
    .history-dates .convert-item{
        width: 100%;
        & input{
            width: 100%;
        }
    }
    .convert-dates{
        height: 6em;
    }
    .custom-select{
        .react-select__menu{
            max-height: 160px !important;
            overflow: hidden;
        }
        .react-select__option{
            padding: 6px;
        }
    }
}

@media (max-width: 480px){
    .convert-items{
        flex-wrap: wrap;
        row-gap: 1em;
        column-gap: 0;
        .convert-item{
            &:nth-child(2), &:nth-child(4){
                width: 40%;
            }
            &:nth-child(3){
                width: 10%;
            }
            & button{
                width: 2em;
                height: 2em;
            }
            & span{
                font-size: 12px;
            }
            .custom-select {
                margin-top: 5px;
            }
        }
    }
    .dates{
        column-gap: 1em;
        display: flex;
        width: 100%;
        justify-content: space-between;
    }
    .convert-dates{
        flex-direction: column-reverse;
        height: auto;
        .convert-btn{
            width: 100%;
        }
    }
    .dates-wrapper{
        margin-top: 1.2em;
        width: 100%;
        display: block;
        .date{
            margin-top: 10px;
            margin-left: 4px;
        }
    }
    .data-item{
        width: 100%;
    }
}